import { Routes } from '@angular/router';

import { MaintenanceMasterComponent } from './maintenance/maintenance.component';
import { ChecklistFormComponent } from './checklist-form/checklist-form.component';
import { CreateChecklistformComponent } from './create-checklistform/create-checklistform.component';
export const SettingsRoutes: Routes = [

    {
        path: '',
        children: [{
            path: 'maintenance',
            component: MaintenanceMasterComponent
        }, {
            path: 'checklist-form',
            component: ChecklistFormComponent
        }, {
            path: 'create-checklistform',
            component: CreateChecklistformComponent
        }]
    }
];



