import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SettingsRoutes } from './settings.routing';
import { MaintenanceMasterComponent } from './maintenance/maintenance.component';
import { ChecklistFormComponent } from './checklist-form/checklist-form.component';
import { CreateChecklistformComponent } from './create-checklistform/create-checklistform.component';
import { FieldErrorDisplayModule } from '../field-error-display/field-error-display.module';
import { MdModule } from '../md/md.module';
import { NguiAutoCompleteModule } from '@ngui/auto-complete';
import { Ng2AutoCompleteModule } from 'ng2-auto-complete';
import { Md2DatepickerModule, MdNativeDateModule } from 'md2';
import { MaterialModule } from '../app.module';
@NgModule({
    imports: [
        CommonModule,
        RouterModule.forChild(SettingsRoutes),
        FormsModule,
        ReactiveFormsModule,
        MdModule,
        MaterialModule,
        NguiAutoCompleteModule,
        Ng2AutoCompleteModule,
        Md2DatepickerModule,
        MdNativeDateModule,
        FieldErrorDisplayModule
    ],
    declarations: [
        MaintenanceMasterComponent,
        ChecklistFormComponent,
        CreateChecklistformComponent
    ]
})

export class SettingsModule { }



