import { Component, OnInit, AfterViewInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Http, Response, RequestOptions, Headers } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Params } from '@angular/router';
import "rxjs/Rx";
import { CommonService } from "../../services/commonService";
import { ChecklistFormService } from "../../services/checklistformService";
import { MaintenanceService } from "../../services/maintenanceService";
import swal from 'sweetalert2';

declare const $: any;

@Component({
    selector: 'app-create-checklistform',
    templateUrl: './create-checklistform.component.html',
    styleUrls: ['./create-checklistform.component.scss'],
    providers: [CommonService, MaintenanceService, ChecklistFormService]
})
export class CreateChecklistformComponent implements OnInit, AfterViewInit {

    public chklistForm = new FormGroup({
        formname: new FormControl(),
        formDescription: new FormControl(),
        workOrdNo: new FormControl(),
        taskdetail: new FormControl(),
        chkActive: new FormControl()
    });

    public formTask = new FormGroup({
        taskdetail: new FormControl()
    });


    public chklistformStatus: any;
    public autoWorkOrdList: any;
    public selectedWOrd: any;
    public chkfomrId: any;
    public hidebutton = true;
    public checkformData: any;
    public checklistFormTaskList; any;
    public fieldArray: any;
    public newAttribute: any;
    public hideTask: boolean = true;
    public userProfile: any;
    public errorMsg: any;
    public isSubmited: boolean = false;

    constructor(
        private http: Http,
        private router: Router,
        private commonService: CommonService,
        private checklistformService: ChecklistFormService,
        private maintenanceService: MaintenanceService,
        private activatedRoute: ActivatedRoute,
        private formBuilder: FormBuilder
    ) { }

    ngOnInit() {
        if (JSON.parse(sessionStorage.getItem('LogEmployee')) != null) {
            this.userProfile = JSON.parse(sessionStorage.getItem('LogEmployee'));
            this.getWorkOrders();
            this.activatedRoute.queryParams.subscribe((params: Params) => {
                if (params['chkfomrid'] == undefined) {
                    this.chklistformStatus = 'Draft';
                }
                else {
                    this.chkfomrId = params['chkfomrid'];
                    this.getChecklistForm(this.chkfomrId);
                    this.getTasks(this.chkfomrId);
                    this.hidebutton = false;
                    this.hideTask = false;
                }
            });

            this.chklistForm = this.formBuilder.group({
                workOrdNo: ['', Validators.nullValidator],
                formname: ['', Validators.required],
                formDescription: ['', Validators.required],
                chkActive: null
            });

            this.formTask = this.formBuilder.group({
                taskdetail: ['', Validators.required]
            });

        } else {
            this.router.navigate(['pages/login']);
        }
    }

    ngAfterViewInit() {

    }

    addFieldValue() {
        this.fieldArray.push(this.newAttribute)
        this.newAttribute = {};
    }

    deleteFieldValue(index) {
        this.fieldArray.splice(index, 1);
    }

    isFieldValid(form: FormGroup, field: string) {
        return !form.get(field).valid && form.get(field).touched;
    }

    displayFieldCss(form: FormGroup, field: string) {
        return {
            'has-error': this.isFieldValid(form, field),
            'has-feedback': this.isFieldValid(form, field)
        };
    }

    showSwal(text: string) {
        swal({
            type: 'success',
            text: text,
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success'

        }).then(() => {
            this.router.navigate(['settings/checklist-form']);
        })
    }

    showStaySwal(text: string) {
        swal({
            type: 'success',
            text: text,
            buttonsStyling: false,
            confirmButtonClass: 'btn btn-success'

        }).catch(swal.noop);
    }

    getWorkOrders() {
        $('.loader').show();
        this.maintenanceService.GetWorkOrderForMaintenance(this.commonService.baseApiUrl, this.userProfile.ID).subscribe(
            data => {
                var dataUsers = JSON.stringify(data);
                var userParse = JSON.parse(dataUsers);
                var userInfo = JSON.parse(userParse["_body"]);
                localStorage.removeItem("AutoWorkOrdList");
                if (userInfo != null) {
                    this.autoWorkOrdList = userInfo;
                    localStorage.setItem("AutoWorkOrdList", JSON.stringify(this.autoWorkOrdList));
                    $('.loader').hide();
                }
            });
    }

    createChecklistForm(status: string) {
        if (this.chklistForm.controls["workOrdNo"].value == undefined || this.chklistForm.controls["workOrdNo"].value == '') {
            this.selectedWOrd = undefined
        }
        $('.loader').show();
        if (this.chklistForm.valid && this.errorMsg == null) {
            var crntUsr = JSON.parse(sessionStorage.getItem('LogEmployee'));
            var contact = {
                ID: this.chkfomrId == undefined ? '0' : this.chkfomrId,
                FormName: this.chklistForm.controls["formname"].value,
                Description: this.chklistForm.controls["formDescription"].value,
                WorkOrderID: this.selectedWOrd == undefined ? null : this.selectedWOrd.ID,
                LastUpdated: null,
                UpdatedBy: crntUsr.ID,
                Status: status
            };

            this.checklistformService.CreateCheckListForm(this.commonService.baseApiUrl, contact).subscribe(
                data => {
                    var dataForm = JSON.stringify(data);
                    var formParse = JSON.parse(dataForm);
                    var formInfo = JSON.parse(formParse["_body"]);
                    if (formInfo != -1) {
                        $('.loader').hide();
                        if (this.chkfomrId != undefined) {
                            this.showSwal("Checklist form has been updated successfully!");
                        }
                        else {
                            this.showSwal("Checklist form has been created successfully!");
                        }
                        //if (this.chkfomrId != undefined) {
                        //    if (status == '1') {
                        //        var checkListData = {
                        //            ChecklistFormID: this.chkfomrId,
                        //            WorkOrderID: this.selectedWOrd == undefined ? null : this.selectedWOrd.ID,
                        //            IsLatest: 1
                        //        };
                        //        this.checklistformService.CreateChecklistFormWorkOrderAssignment(this.commonService.baseApiUrl, this.userProfile.ID, checkListData).subscribe(
                        //            data => {
                        //                var dataAForm = JSON.stringify(data);
                        //                var formAParse = JSON.parse(dataAForm);
                        //                var formAInfo = JSON.parse(formAParse["_body"]);
                        //                if (formAInfo == true) {
                        //                    $('.loader').hide();
                        //                    this.showSwal("Checklist form has been updated successfully!!");
                        //                }
                        //            });
                        //    }
                        //}
                        //else {
                        //    this.showSwal("Checklist form has been created successfully!");
                        //}
                    }
                });
        }
        else {
            $('.loader').hide();
            this.validateAllFormFields(this.chklistForm);
        }
    }

    updateCheckListFormStatus() {
        $('.loader').show();
        if (this.chklistForm.valid) {
            var crntUsr = JSON.parse(sessionStorage.getItem('LogEmployee'));
            var contact = {
                ID: this.chkfomrId,
                LastUpdated: null,
                UpdatedBy: crntUsr.ID,
                Status: this.chklistForm.controls["chkActive"].value == true ? 1 : 2
            };
            this.checklistformService.UpdateCheckListFormStatus(this.commonService.baseApiUrl, contact).subscribe(
                data => {
                    var dataForm = JSON.stringify(data);
                    var formParse = JSON.parse(dataForm);
                    var formInfo = JSON.parse(formParse["_body"]);
                    if (formInfo == true) {
                        $('.loader').hide();
                        this.showSwal("Form updated successfully!!");
                    }
                });
        }
        else {
            $('.loader').hide();
            this.validateAllFormFields(this.chklistForm);
        }
    }

    checkChkListFormListExist(event: any) {
        this.errorMsg = null;
        var formName = event.target.value;
        var formID = this.chkfomrId == undefined ? '0' : this.chkfomrId;
        this.checklistformService.CheckChkListFormListExist(this.commonService.baseApiUrl, formName, formID).subscribe(
            data => {
                var dataEmp = JSON.stringify(data);
                var empParse = JSON.parse(dataEmp);
                var empSts = JSON.parse(empParse["_body"]);
                if (empSts == false) {
                    $("#lblFName").html('');
                    this.errorMsg = "Form Name (already exists)";
                }
                else {
                    $('#lblFName').html('Form Name <small> (required) </small>');
                    this.errorMsg = null;
                }
            }
        );
    }

    getChecklistForm(contact: any) {
        $('.loader').show();
        return this.checklistformService.GetCheckListFormByID(this.commonService.baseApiUrl, contact).subscribe(
            data => {
                var dataMt = JSON.stringify(data);
                var mTParse = JSON.parse(dataMt);
                var mTList = JSON.parse(mTParse["_body"]);
                if (mTList != null) {
                    this.checkformData = mTList;
                    var elems = document.querySelectorAll(".label-floating");
                    if (elems != null) {
                        [].forEach.call(elems, function (el) {
                            el.classList.remove("is-empty");
                        });
                    }
                    if (this.checkformData.WorkOrderID != null) {
                        //this.filterItem(this.checkformData.WorkOrderID);
                        var autoWOData = JSON.parse(localStorage.getItem("AutoWorkOrdList")).filter(item => item.ID == this.checkformData.WorkOrderID);
                        this.selectedWOrd = autoWOData[0];
                        console.log(this.selectedWOrd);
                        this.chklistForm.controls["workOrdNo"].setValue(this.selectedWOrd.WorkOrderNo);
                    }
                    else {
                        this.selectedWOrd = undefined;
                    }
                    this.chklistForm.controls["formname"].setValue(this.checkformData.FormName);
                    this.chklistForm.controls["formDescription"].setValue(this.checkformData.Description);
                    this.chklistForm.controls["chkActive"].setValue(this.checkformData.Status == 1 ? true : false);
                    this.chklistformStatus = this.checkformData.Status == 1 ? "Active" : (this.checkformData.Status == 2 ? "InActive" : "Draft");
                    $('.loader').hide();
                }
            }
        );
    }

    createNewTask() {
        $('.loader').show();
        if (this.formTask.valid) {
            var contact = {
                ID: 0,
                ChecklistFormVersionID: null,
                Task: this.formTask.controls["taskdetail"].value,
                TaskOrder: 0
            };
            console.log(contact);
            this.checklistformService.CreateNewTask(this.commonService.baseApiUrl, this.chkfomrId + '-' + JSON.parse(sessionStorage.getItem('LogEmployee')).ID, contact).subscribe(
                data => {
                    var dataUsers = JSON.stringify(data);
                    var userParse = JSON.parse(dataUsers);
                    var userInfo = JSON.parse(userParse["_body"]);
                    $('#myModal').modal('toggle');
                    if (userInfo == 1) {
                        //this.getWorkOrderAssignment(localStorage.getItem("WorkOrderId"));
                        this.showStaySwal("New task has been created.");
                        this.getTasks(this.chkfomrId);
                    }
                    else if (userInfo == "already") {
                        this.showSwal("Work Order already assigned!!");
                    }
                    this.formTask.reset();
                    $('.loader').hide();
                });
        }
        else {

        }
    }

    deleteTask(taskId: any) {
        swal({
            title: 'Are you sure?',
            text: 'You will not be able to revert this!',
            type: 'warning',
            showCancelButton: true,
            confirmButtonClass: 'btn btn-success',
            cancelButtonClass: 'btn btn-danger',
            confirmButtonText: 'Yes, delete it!',
            buttonsStyling: false
        }).then(() => {
            $('.loader').show();
            this.checklistformService.DeleteCheckListFormTask(this.commonService.baseApiUrl, taskId).subscribe(
                data => {
                    var dataUsers = JSON.stringify(data);
                    var userParse = JSON.parse(dataUsers);
                    var userInfo = JSON.parse(userParse["_body"]);
                    if (userInfo == true) {
                        this.getTasks(this.chkfomrId);
                        $('.loader').hide();
                        swal({
                            title: 'Deleted!',
                            text: 'User has been deleted.',
                            type: 'success',
                            confirmButtonClass: 'btn btn-success',
                            buttonsStyling: false
                        }).catch(swal.noop);
                    }
                });
        }).catch(swal.noop);
    }

    getTasks(chkformId: any) {
        $('.loader').show();
        this.checklistformService.GetTasksByFormID(this.commonService.baseApiUrl, chkformId).subscribe(
            data => {
                var datawAsg = JSON.stringify(data);
                var wAsgParse = JSON.parse(datawAsg);
                var wAsgList = JSON.parse(wAsgParse["_body"]);
                this.checklistFormTaskList = null;
                if (wAsgList != null && wAsgList.length > 0) {
                    this.checklistFormTaskList = wAsgList;
                    this.isSubmited = true;
                }
                else {
                    this.isSubmited = false;
                }
            }
        );
    }

    public myListFormatter(data: any): string {
        return data['WorkOrderNo'];
    }

    valueChanged(newVal) {
        this.selectedWOrd = newVal;
    }

    //filterItem(value) {
    //    if (this.autoWorkOrdList == null) {
    //        this.getWorkOrders();
    //    }
    //    else {
    //        var dataMt = JSON.stringify(Object.assign([], this.autoWorkOrdList).filter(item => item.ID == value));
    //        var mTParse = JSON.parse(dataMt);
    //        this.selectedWOrd = mTParse[0];
    //        //this.selectedWOrd = mTParse[0].WorkOrderNo;
    //    }
    //}

    validateAllFormFields(formGroup: FormGroup) {
        Object.keys(formGroup.controls).forEach(field => {
            const control = formGroup.get(field);
            if (control instanceof FormControl) {
                control.markAsTouched({ onlySelf: true });
            } else if (control instanceof FormGroup) {
                this.validateAllFormFields(control);
            }
        });
    }
}



